
import * as types from './mutation-types'

export default {
    // 增加总时间
  [types.ADD_TOTAL_TIME] (state, time) {

    //检查数据类型
    console.log("state.totalTime--> " + typeof(state.totalTime))
    console.log("time--> " + typeof(time))

    //string转number...
    state.totalTime = state.totalTime + Number(time)
  },
  // 减少总时间
  // [types.DEC_TOTAL_TIME] (state, time) {
  //   state.totalTime = state.totalTime - time
  // },
  // 新增计划
  [types.SAVE_PLAN] (state, plan) {
    // 设置默认值，未来我们可以做登入直接读取昵称和头像
    // const avatar = 'https://avatars0.githubusercontent.com/u/15359544?v=3&s=460';

    state.list.push(plan)
    
      var LH = Object.assign({}, plan)
      LH = window.localStorage

       var storage=window.localStorage;
            //写入a字段
            storage["a"]=1;
            //写入b字段
            storage.b=1;
            //写入c字段
            storage.setItem("c",3);
            console.log('写入a字段'+ typeof storage["a"]);
            console.log(typeof storage["b"]);
            console.log(typeof storage["c"]);
       
  },
  // 删除某计划
  // [types.DELETE_PLAN] (state, idx) {
  //   state.list.splice(idx, 1);
  // }
};



