<template>
  <div>
  <div v-if="$route.path !== '/time-entries/log-time'">
    <router-link to="/time-entries/log-time" 
      class="btn create-plan">
      创建
    </router-link>
    <!-- <p>新建任务</p> -->
  </div>
    <router-view></router-view>
    <div v-for="plan in plans">
      <p>
        时间：
        {{ plan.totalTime }}
      </p>
      <hr/>
    </div>

  </div>
</template>

<script>
  export default {
    // name : 'TimeEntries',
    computed : {
      plans () {
        return this.$store.state.list
      }
    }
  }
</script>



